<script>
import moment from 'moment'

export default {
  name: 'CurrentDateTimeComponent',
  data () {
    return {
      intervalId: undefined
    }
  },
  created () {
    this.refreshDateAndTime()
    this.intervalId = setInterval(() => this.refreshDateAndTime(), 1000)
  },
  methods: {
    refreshDateAndTime () {
      this.$emit('timeChanged', {
        cTime: moment().format('HH:mm:ss'),
        cDate: moment().format('Do MMM YYYY'),
        cDay: moment().format('ddd'),
        cFullDay: moment().format('dddd') })
    }
  }
}
</script>

