<template>
  <div class="home">
    <div v-if="!quote || !quote.text">
      <div class="quote-text-container">
        <div class="quote-text loader"></div>
      </div>
    </div>
    <div v-else>
      <div class="top-text-container">
        <div class="settings-container">
          <font-awesome-icon icon="cog" size="2x" spin inverse @click="isChangingSettings = true" />
          <p class="refresh-rate-text">{{ selectedRefreshOption.text }}</p>
        </div>
        <div class="time-text thick">{{ showTime ? currentMomentData.cTime : '' }}</div>
        <div class="date-text" v-if="showDay && !showDate">{{ currentMomentData.cFullDay }}</div>
        <div class="date-text" v-else>{{ showDay ? currentMomentData.cDay : '' }}{{ showDay && showDate ? ', ' : '' }}{{ showDate ? currentMomentData.cDate : '' }}</div>
      </div>
      <div class="author-text-container">
        <h6 class="author-text">[ {{ quote.author }} ]</h6>
      </div>
      <div class="quote-text-container">
        <h1 class="quote-text thick"><b>{{ quote.text }}</b></h1>
      </div>
    </div>
    <settings-modal v-if="isChangingSettings"
                    @cancel="isChangingSettings = false"
                    @confirm="settingsChanged"
                    :refreshOption="selectedRefreshOption"
                    :showTime="showTime"
                    :showDate="showDate"
                    :showDay="showDay" />
    <current-date-time-component @timeChanged="timeChanged" />
  </div>
</template>

<script>
import SettingsModal from '@/components/SettingsModal'
import axios from 'axios'
import CurrentDateTimeComponent from '@/components/CurrentDateTimeComponent'

export default {
  name: 'home',
  components: { CurrentDateTimeComponent, SettingsModal },
  created () {
    this.track()
    this.retrieveQuoteCount()
      .then(response => {
        this.quoteIdMap = this.randomlyAllocatedArray(response.data.ItemCount)

        this.retrieveQuote()
        this.initializeInterval(this.retrieveQuote, this.selectedRefreshOption.value)
      })
  },
  data () {
    return {
      apiGatewayUrl: process.env.VUE_APP_API_GATEWAY_URL,
      quote: undefined,
      quoteIdMap: undefined,
      quoteIdIndex: 0,
      intervalId: undefined,
      isChangingSettings: false,
      selectedRefreshOption: { text: 'refresh every 24 hours', value: 24.00 },
      currentMomentData: undefined,
      showTime: true,
      showDay: true,
      showDate: true
    }
  },
  methods: {
    track () {
      this.$ga.page('/')
    },
    retrieveQuote () {
      axios.post(`${this.apiGatewayUrl}/quote/${this.quoteIdMap[this.quoteIdIndex]}`, {}, this.config())
        .then(response => {
          this.safeIndexIncrement()
          this.quote = response.data.Item
        })
    },
    retrieveQuoteCount () {
      return axios.post(`${this.apiGatewayUrl}/quote/count`, {}, this.config())
    },
    initializeInterval (callback, refreshHours) {
      this.intervalId = setInterval(() => callback(), this.toMillis(refreshHours)) // hours
    },
    config () {
      return {
        headers: {
          'X-API-KEY': 'quvASESLkjh0F0xnsyJV4wFEFqOcIzP5zjbCiOk9'
        }
      }
    },
    randomlyAllocatedArray (totalCount) {
      let array = []
      let random

      for (let i = 0; i < totalCount; i++) {
        do {
          random = this.getRandomBetween(1, totalCount)
        } while (array.length > 0 && array.includes(random))

        array.push(random)
      }

      return array
    },
    getRandomBetween (from, to) {
      return Math.floor(Math.random() * to) + from
    },
    safeIndexIncrement () {
      this.quoteIdIndex++

      if (this.quoteIdIndex >= this.quoteIdMap.length) {
        this.quoteIdIndex = 0
      }
    },
    toMillis (hours) {
      return 1000 * 60 * 60 * hours
    },
    settingsChanged (settings) {
      if (settings.selectedOption.value !== this.selectedRefreshOption.value) {
        this.selectedRefreshOption = settings.selectedOption
        clearInterval(this.intervalId)
        this.initializeInterval(this.retrieveQuote, settings.selectedOption.value)
      }

      this.showTime = settings.showTime
      this.showDate = settings.showDate
      this.showDay = settings.showDay

      this.isChangingSettings = false
    },
    timeChanged (data) {
      this.currentMomentData = data
    }
  }
}
</script>

