<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">

          <div class="modal-header">Configuration</div>

          <div class="modal-body">
            <label for="refresh-rate-select">Quote Refresh Rate:</label>
            <select id="refresh-rate-select" class="modal-body-child" v-model="settingsModel.selectedOption">
              <option v-for="option in refreshOptions" :value="option" v-bind:key="option.value">
                {{ option.text }}
              </option>
            </select>
            <span class="modal-body-child">
              <input type="checkbox" id="time-checkbox" v-model="settingsModel.showTime">
              <label for="time-checkbox">Show Time</label>
            </span>
            <span class="modal-body-child">
              <input type="checkbox" id="date-checkbox" v-model="settingsModel.showDate">
              <label for="date-checkbox">Show Date</label>
            </span>
            <span class="modal-body-child">
              <input type="checkbox" id="day-checkbox" v-model="settingsModel.showDay">
              <label for="day-checkbox">Show Day of Week</label>
            </span>
          </div>

          <div class="modal-footer">
            <button class="modal-default-button" @click="$emit('cancel')">Cancel</button>
            <button class="modal-default-button" @click="confirm">OK</button>
          </div>

          <p><a href="mailto:pavelski.t@gmail.com"><h6>Contact developer</h6></a></p>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'SettingsModal',
  props: [ 'refreshOption', 'showTime', 'showDate', 'showDay' ],
  created () {
    this.settingsModel.selectedOption = this.refreshOption
    this.settingsModel.showTime = this.showTime
    this.settingsModel.showDate = this.showDate
    this.settingsModel.showDay = this.showDay
    this.track()
  },
  data () {
    return {
      refreshOptions: [
        { text: 'refresh every 5 minutes', value: 0.083334 },
        { text: 'refresh every 15 minutes', value: 0.25 },
        { text: 'refresh every 30 minutes', value: 0.50 },
        { text: 'refresh every 1 hour', value: 1.00 },
        { text: 'refresh every 2 hours', value: 2.00 },
        { text: 'refresh every 4 hours', value: 4.00 },
        { text: 'refresh every 6 hours', value: 6.00 },
        { text: 'refresh every 12 hours', value: 12.00 },
        { text: 'refresh every 24 hours', value: 24.00 }
      ],
      settingsModel: {
        selectedOption: undefined,
        showTime: true,
        showDay: true,
        showDate: true
      }
    }
  },
  methods: {
    confirm () {
      this.$emit('confirm', this.settingsModel)
    },
    track () {
      this.$ga.page('/settings-modal')
    }
  }
}
</script>

